h1 {
    color: #fe538a;
    margin: 0 0 25px 0;
}

input[type=text] {
    &:focus {
        outline: none;
    }
}

input[type=email] {
    &:focus {
        outline: none;
    }
}

input[type=password] {
    &:focus {
        outline: none;

    }

}

input[type=text]:focus {
    border: 1px solid #fe538a;
}

input[type=email]:focus {
    border: 1px solid #fe538a;
}

input[type=password]:focus {
    border: 1px solid #fe538a;
}

input[type=text]:hover {
    border: 1px solid #fe538a;
}

input[type=email]:hover {
    border: 1px solid #fe538a;
}

input[type=password]:hover {
    border: 1px solid #fe538a;
}


.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    border: 1px solid black;
}

.register-form {
    border: 1px solid #fe538a;
    padding: 30px;
    width: 25%;

    &:focus {
        outline: none;
    }

}

.btn {
    background-color: #fe538a;
    font-weight: bold;
    color: #FFFFFF;
}

.btn:hover {
    background-color: #FFFFFF;
    color: #fe538a;
    border: 1px solid #fe538a;
    transition: 0.3s;
    font-weight: bold;

    &:focus {
        outline: none;
    }
}

.btn-container {
    display: flex;
    justify-content: space-between;
}

.label-field {
    font-weight: 500;
}