.main {
    padding: 10px;
    height: 100vh;
}

.layout {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.sidebar {
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: #fe538a;
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    color: #FFFFFF;
}

.content {
    width: 100%;
    height: 100%;
}

.header {
    height: 10vh;
    margin: 0 0 20px 0;
    box-shadow: 0 0 2px grey;
    background-color: #FFFFFF;
}

.body {
    height: 80vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: #FFFFFF;
}

.logo {
    font-size: 1.5rem;
    margin: 20px 0 0 0;
    text-align: center;
}

.menu {
    margin: 20px 0 0 0;
}

.menu-item {
    margin: 10px 0 0 0;
}

.menu-item:hover {
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    transition: 0.5s;
    box-shadow: 1px rgba(0, 0, 0, 0.1);
    border-bottom: 0.5px solid #FFFFFF;
    padding: 2px;
}

.menu-item a {
    text-decoration: none;
    color: #FFFFFF;
    font-size: 18px;
    padding: 5px;
}

/* .menu-item a:hover {
    color: #fe538a;
    background-color: #FFFFFF;
    padding: 5px;
} */

.menu-item i {
    margin: 0 20px 0 20px;
    font-size: 18px;
    padding: 5px;
    color: #FFFFFF;
}

.active {
    border-radius: 5px;
    background-color: #FFFFFF;
    color: #fe538a;
    padding: 3px;
}

.active a,
.active i {
    /* background-color: #FFFFFF; */
    color: #fe538a;
    padding: 5px;
}

.header-content {
    display: flex;
    /* flex-direction: row; */
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    height: 50px;
    margin: 0 15px 0 15px;
}

.header-content i {
    margin: 0 5px 0 0;
    font-size: 18px;
    padding: 5px;
    color: #ffc800;
}

.header-content a {
    color: #696969;
    text-decoration: none;
    margin: 0 3px 0 10px;
    font-size: 14px;
    text-transform: uppercase;
}

.header-content a:hover {
    color: #000000;
    font-weight: bold;
    font-size: 14px;

}