h1 {
    color: #fe538a;
    margin: 0 0 25px 0;
}

input[type=email] {
    &:focus {
        outline: none;
    }
}

input[type=password] {
    &:focus {
        outline: none;

    }

}

input[type=email]:focus {
    border: 1px solid #fe538a;
}

input[type=password]:focus {
    border: 1px solid #fe538a;
}

input[type=email]:hover {
    border: 1px solid #fe538a;
}

input[type=password]:hover {
    border: 1px solid #fe538a;
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 5px;
}

.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    border: 1px solid black;
}

.login-form {
    /* border: 1px solid gray; */
    padding: 30px;
    width: 25%;
}

.login-form {
    &__title {
        text-align: center;
        margin-bottom: 20px;
    }

    &__input {
        margin-bottom: 10px;
    }

    &__input-label {
        display: block;
        margin-bottom: 5px;
    }

    &__input-field {
        border: 1px solid #fe538a;

        &:focus {
            outline: none;
        }
    }

    &__input-field--error {
        border: 1px solid red;
    }

    &__input-error {
        color: red;
        margin-top: 5px;
    }

    &__input-field--error:focus {
        outline: none;
    }

}

.btn {
    background-color: #fe538a;
    font-weight: bold;
    color: #FFFFFF;

}

.btn:hover {
    background-color: #FFFFFF;
    color: #fe538a;
    border: 1px solid #fe538a;
    transition: 0.3s;
    font-weight: bold;

    &:focus {
        outline: none;
    }
}

.btn-container {
    display: flex;
    justify-content: space-between;
}

.label-field {
    font-weight: 500;
}