* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}


/* Loader (Spinner) */
.spinner {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.4);
    color: #fe538a;
}

/* ApplyDoctor.js */
.register-doctor-heading {
    color: #64d2fe;
}

.btn form-btn {
    background-color: #64d2fe;
    color: #FFFFFF;
    width: 250px !important;
    margin: 30px 0 0 0 !important;
}

.form-btn {
    background-color: #64d2fe;
    color: #FFFFFF;
    width: 250px !important;
    margin: 30px 0 0 0 !important;
}

.form-btn:hover {
    background-color: #FFFFFF;
    color: #64d2fe;
    border: 1px solid #64d2fe;

}

/* Hospital.js */

.search-container {
    display: flex;
    align-items: center;
    /* Align items vertically */
}

.search-input {
    height: 40px;
    /* Adjust height as needed */
    flex: 1;
    /* Take remaining space */
    margin-right: 5px;
    /* Add some spacing between input and button */
}

.btn-search {
    height: 40px;
    width: 100px;
    background-color: #fe538a;
    color: #FFFFFF;
}

.hospital-container {
    display: flex;
    justify-content: center;
}

.map-container {
    width: 400px;
    height: 200px;
    margin-right: 20px;
}

.hospital-results {
    width: 400px;
    height: 400px;
    overflow-y: auto;
}

.hospital-results h2 {
    margin-bottom: 10px;
    /* Add some spacing below the heading */
}